.rhombusBox {
    display: flex;
    align-items: center;
    align-content: center;
}

.rhombusBoxLeftLine,
.rhombusBoxRightLine {
    height: 2px;
    flex-grow: 1;
    background-color: var(--c-black);
}

.rhombusBoxRhombus {
    width: 10px;
    height: 10px;
    margin: 0 5px;
    position: relative;
}

.rhombusBoxRhombus svg {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

.rhombusBoxRhombus svg rect {
    fill: var(--c-black);
}

.root {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    background-color: var(--c-white);
    font-family: var(--font-lato);
}

.root a {
    color: #4E0A7B;
    text-decoration: underline;
}

.root a:hover {
    text-decoration: none;
}

.article,
.previewImage {
    min-width: 100%;
    max-width: 100%;
}

.article {
    padding: 20px;
    font-size: 16px;
    background-color: var(--c-white);
    order: 1;
}

.previewImage {
    position: relative;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: cover;
    order: 0;
    height: 284px;
}

.title {
    text-align: center;
    text-transform: uppercase;
    font-size: 24px;
    font-weight: 700;
    line-height: 30px;
    padding: 12px 0;
}

.subTitle {
    font-size: 16px;
    font-weight: 300;
    line-height: 22px;
    text-align: center;
    padding-bottom: 10px;
    border-bottom: 2px solid var(--c-black);
    margin-bottom: 20px;
}

.announce {
    font-size: 14px;
    line-height: 22px;
    font-weight: 300;
    margin-bottom: 20px;
}

p {
    font-size: 14px;
    line-height: 22px;
    margin-bottom: 20px;
}

.info,
.videoBox {
    padding-top: 20px;
    font-family: var(--font-lato);
    border-top: 2px solid var(--c-black);
}

.infoPersonTitle {
    font-size: 18px;
    text-transform: uppercase;
    margin-bottom: 10px;
}

.infoPersonName {
    font-weight: 300;
    line-height: 1;
}

.infoPersonSurname {
    font-weight: 900;
    line-height: 1;
    margin-left: 6px;
}

.infoPersonDescription {
    font-size: 12px;
    font-weight: 600;
    text-transform: uppercase;
    margin-bottom: 24px;
}

.infoPersonPhoto {
    margin-bottom: 20px;
}

.infoPersonPhotoImage {
    display: block;
    width: 100%;
}

.infoPersonPhotoDescription {
    font-size: 14px;
    font-weight: 400;
    color: #7B7B7B;
}

.videoBox {
    position: relative;
    padding-bottom: 40px;
}

.videoBoxVideo {
    display: block;
    outline: none;
    border: 0 none;
    border-radius: 0;
    width: 100%;
}

.videoBoxDescription {
    position: relative;
    font-size: 14px;
    padding: 12px 18px;
    color: var(--c-white);
    background-color: var(--c-black);
}

@media screen and (min-width: 1024px) {
    .root {
        flex-direction: row;
    }

    .title {
        font-size: 44px;
        line-height: 54px;
        padding: 30px 0;
    }

    .subTitle {
        font-size: 36px;
        font-weight: 300;
        line-height: 44px;
        padding-bottom: 40px;
        margin-bottom: 40px;
    }

    .announce {
        font-size: 22px;
        line-height: 32px;
        margin-bottom: 40px;
    }

    p {
        font-size: 18px;
        line-height: 24px;
        margin-bottom: 40px;
    }

    .info,
    .videoBox {
        padding-top: 40px;
    }

    .article,
    .previewImage {
        min-width: 50%;
        max-width: 50%;
    }

    .article {
        padding: 90px;
        order: 0;
        font-size: 20px;
    }

    .previewImage {
        position: fixed;
        height: 100vh;
        left: 50%;
        order: 1;
    }

    .infoPersonTitle {
        font-size: 24px;
        margin-bottom: 20px;
    }

    .infoPersonPhoto {
        margin-bottom: 60px;
    }
}