.socials {
    display: flex;
    align-items: center;
    align-content: center;
}

.socialItem {
    position: relative;
    width: 24px;
    height: 24px;
    background: none;
    border: 0 none;
    cursor: pointer;
    margin-left: 10px;
    outline: none;
}

.socialItem:first-child {
    margin-left: 0;
}

.socialItemIcon {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.socialItemIcon svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

@media screen and (min-width: 1024px) {
    .socialItem {
        position: relative;
        width: 32px;
        height: 32px;
    }
}