.root {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url("../../../src/assets/images/hotel.png");
    background-position: 50% 50%;
    min-width: 320px;
    min-height: 568px;
    overflow: hidden;
}

.root::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 197px;
    z-index: 5;
    background: linear-gradient(180deg, #2E1A24 0%, rgba(69, 41, 59, 0.7) 33.64%, rgba(101, 56, 49, 0.4) 58.41%, rgba(101, 56, 49, 0) 100%);
}

.header {
    margin-top: 30px;
    position: relative;
    z-index: 10;
}

.logo {
    position: relative;
    width: 200px;
    height: 130px;
    margin: 0 auto;
}

.logo svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.content {
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    padding: 10px;
    z-index: 20;
    transform: translateY(-19%);
}

.manPicture {
    position: absolute;
    z-index: 10;
    bottom: 0;
    height: 76.95774647887324%;
    margin-left: 50%;
    transform: translateX(-30%);
}

.footer {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 7.922535211267606%;
    background-image: url("../../../src/assets/images/table.png");
    background-repeat: repeat-x;
    background-size: cover;
    background-color: #cbb1b0;
}

.gotoRG {
    line-height: 1;
    text-align: center;
}

.gotoRGLink {
    font-size: 26px;
    font-family: var(--font-lato);
    font-weight: 900;
    line-height: 1;
    text-transform: uppercase;
    color: var(--c-white);
}

.gotoRGLink:hover {
    color: var(--c-white);
}

.enter {
    text-align: center;
    font-size: 26px;
    font-family: var(--font-lato);
    font-weight: 900;
    line-height: 1;
    text-transform: uppercase;
    color: var(--c-white);
    margin-bottom: 13px;
    cursor: pointer;
}

.rhombusBox {
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
    margin-bottom: 8px;
}

.rhombusBox::before,
.rhombusBox::after {
    content: '';
    width: 106px;
    height: 2px;
    background-color: var(--c-white);
}

.rhombusBoxIcon {
    position: relative;
    margin: 0 11px;
    min-width: 13px;
    min-height: 13px;
    max-width: 13px;
    max-height: 13px;
}

.rhombusBoxIcon svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.play {
    margin: 0 auto;
}

.playIcon {
    position: relative;
    width: 80px;
    height: 80px;
    margin: 0 auto 35px;
}

.playIcon svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

@media screen and (min-width: 768px) {
    .root {
        min-height: 768px;
    }

    .logo {
        width: 450px;
        height: 292px;
    }

    .content {
        transform: translateY(-14%);
    }

    .manPicture {
        height: 69.6318359375%;
    }

    .footer {
        height: 7.421875%;
    }

    .manPicture {
        transform: translateX(-21%);
    }

    .playIcon {
        width: 150px;
        height: 150px;
        margin-bottom: 70px;
    }

    .gotoRGLink,
    .enter {
        font-size: 54px;
    }

    .enter {
        margin-bottom: 26px;
    }

    .rhombusBox {
        margin-bottom: 19px;
    }

    .rhombusBox::before,
    .rhombusBox::after {
        width: 224px;
    }

    .rhombusBoxIcon {
        min-width: 22px;
        min-height: 22px;
        max-width: 22px;
        max-height: 22px;
        margin: 0 19px;
    }
}

@media screen and (min-width: 1024px) {
    .root {
       /* min-height: 768px;*/
    }
    .header {
        margin-top: 40px;
        z-index: 25;
    }

    .logo {
        width: 550px;
        height: 356px;
    }

    .content {
        transform: translateY(-14%);
    }

    .manPicture {
        height: 73.78125%;
        transform: translateX(0);
    }

    .footer {
        height: 7.682291666666667%;
    }

    .playIcon {
        position: relative;
        margin-left: 50%;
        top: 170px;
        left: 332px;
        width: 86px;
        height: 86px;
        cursor: pointer;
    }

    .gotoRGLink,
    .enter {
        font-size: 48px;
    }
}

@media screen and (min-width: 1440px) {
    .root {
       /* min-height: 1050px;*/
    }
    .header {
        margin-top: 61px;
    }

    .logo {
        width: 700px;
        height: 454px;
    }

    .gotoRGLink,
    .enter {
        font-size: 72px;
    }

    .rhombusBox::before,
    .rhombusBox::after {
        width: 310px;
    }

    .playIcon {
        width: 130px;
        height: 130px;
        top: 210px;
        left: 414px;
    }
}
