.header {
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 10;
}

.burger {
    position: absolute;
    top: 20px;
    right: 20px;
    width: 40px;
    height: 40px;
    background: none;
    outline: none;
    border: 0 none;
    margin: 0;
    padding: 0;
}

:root :global(.libActive) .header,
:root :global(.movieActive) .header,
:root :global(.atriumActive) .header,
:root :global(.healthActive) .header {
    z-index: 1000000;
}

:root :global(.libActive) .menuItemTitle,
:root :global(.movieActive) .menuItemTitle,
:root :global(.atriumActive) .menuItemTitle,
:root :global(.healthActive) .menuItemTitle {
    position: relative;
    z-index: -100000;
    opacity: 0.4;
}

:root :global(.libActive) .menuItemRhombus,
:root :global(.movieActive) .menuItemRhombus,
:root :global(.atriumActive) .menuItemRhombus,
:root :global(.healthActive) .menuItemRhombus {
    z-index: -100000;
    opacity: 0.4;
}

:root :global(.libActive) .menuItemTitleActive,
:root :global(.movieActive) .menuItemTitleActive,
:root :global(.atriumActive) .menuItemTitleActive,
:root :global(.healthActive) .menuItemTitleActive {
    opacity: 1;
    z-index: 10000000;
    cursor: default;
}

:root :global(.libActive) .burger,
:root :global(.movieActive) .burger,
:root :global(.atriumActive) .burger,
:root :global(.healthActive) .burger,
:root :global(.mobileActive) .burger,
:root :global(.personMenuActive) .burger {
    display: none;
}

:root :global(.mobileActive) .floor,
:root :global(.mobileActive) .window,
:root :global(.mobileActive) .person,
:root :global(.personMenuActive) .floor,
:root :global(.personMenuActive) .window,
:root :global(.personMenuActive) .person,
:root :global(.libActive) .floor,
:root :global(.libActive) .window,
:root :global(.libActive) .person,
:root :global(.libActive) :global(.part),
:root :global(.overflowed) :global(.part) {
    visibility: hidden;
}

.logo {
    display: block;
    position: absolute;
    width: 140px;
    height: 90px;
    top: 20px;
    margin-top: -2px;
    margin-left: 50%;
    left: -70px;
    pointer-events: none;
}

.logo svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.borderLayer {
    position: fixed;
    z-index: 20;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    pointer-events: none;
}

.borderLayerLine {
    position: absolute;
    background-color: var(--c-white);
}

.borderLayerFooter {
    display: flex;
    align-items: center;
    align-content: center;
    position: absolute;
    left: 2vw;
    right: 2vw;
    bottom: 4vh;
    padding: 20px;
    pointer-events: auto;
}

.gotoRG {
    margin-left: auto;
    margin-right: 2vw;
    display: none;
}

.gotoRGLink {
    display: block;
    position: relative;
    width: 133px;
    height: 21px;
}

.gotoRGLink svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.lineTop {
    display: none;
    height: 2px;
    top: 4vh;
    left: 2vw;
    right: 2vw;
    margin-left: 30px;
    margin-right: 30px;
}

.lineBottom {
    display: none;
    height: 2px;
    bottom: 4vh;
    left: 2vw;
    right: 2vw;
    margin-left: 30px;
    margin-right: 30px;
}

.lineLeft {
    display: none;
    left: 2vw;
    top: 4vh;
    bottom: 4vh;
    width: 2px;
    margin-top: 30px;
    margin-bottom: 30px;
}

.lineRight {
    display: none;
    right: 2vw;
    top: 4vh;
    bottom: 4vh;
    width: 2px;
    margin-top: 30px;
    margin-bottom: 30px;
}

.corner {
    display: none;
    position: absolute;
    width: 30px;
    height: 30px;
}

.cornerTop {
    top: 4vh;
}

.cornerLeft {
    left: 2vw;
}

.cornerRight {
    transform: rotate(90deg);
    right: 2vw;
}

.cornerBottom {
    bottom: 4vh;
    transform: rotate(180deg);
}

.cornerLeft.cornerBottom {
    transform: rotate(270deg);
}

.floor {
    display: block;
    width: 150px;
    height: 492px;
    position: fixed;
    top: 100%;
    left: -75px;
    margin-left: 50%;
    margin-top: -250px;
    pointer-events: auto;
}

    .floorRound svg :global(path.part) {
        visibility: hidden;
    }

.floorRound,
.floorRound svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.floorValue {
    display: none;
    color: var(--c-white);
    font-family: var(--font-aesthet-nova);
    font-weight: 500;
    line-height: 1;
    cursor: pointer;
}

:root :global(.overflowed) .floorValue {
    visibility: hidden;
}

.floorCurrent {
    display: block;
    font-size: 22px;
    top: 50%;
    position: absolute;
    margin-left: 50%;
    width: 60px;
    text-align: center;
    left: -30px;
    margin-top: -24px;
}

.floorCurrentMiddle {
    font-size: 70px;
}

.floorPrev,
.floorNext {
    width: 60px;
    position: absolute;
    left: -29px;
    margin-left: 50%;
    font-size: 24px;
    text-align: center;
}

.floorPrev {
    top: 60px;
}

.floorNext {
    top: 100%;
    margin-top: -84px;
}

.frame {
    position: fixed;
    z-index: 10;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    pointer-events: none;
}


.frame::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 440px;
    background: linear-gradient(180deg, rgba(46,26,36,1) 0%, rgba(69,41,59,0.7) 30%, rgba(101,56,49,0.4) 60%, rgba(101,56,49,0) 100%);
}

.section {
    position: relative;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: cover;
}

.section:last-child {
    overflow: hidden;
}

.section:nth-child(3n+1) {
    background-image: url('../../assets/images/background_gray.jpg');
}

.section:nth-child(3n+2) {
    background-image: url('../../assets/images/background_rose.jpg');
}

.section:nth-child(3n+3) {
    background-image: url('../../assets/images/background_yellow.jpg');
}

.sectionParapet {
    width: 100%;
    height: 36px;
    position: absolute;
    z-index: 1;
    left: 0;
    bottom: -22px;
    background-repeat: repeat-x;
    background-image: url('../../assets/images/pg.png');
    background-size: contain;
}

.sectionInner {
    position: relative;
}

.sectionInner::after,
.sectionInner::before {
    width: 29px;
    content: '';
    position: absolute;
    background-repeat: repeat-y;
    top: 0;
    left: 0;
    bottom: 0;
    background-size: contain;
}

.sectionInner::before {
    background-image: url("../../assets/images/brick_l.png");
}

.sectionInner::after {
    background-image: url("../../assets/images/brick_r.png");
    left: auto;
    right: 0;
}

.panelShow {
    /*background: red;*/
}

.section :global(.panel) {
    position: relative;
    padding-top: 40px;
}

.section :global(.panel:nth-child(even)) {
    padding-bottom: 40px;
}

.root :global(.panel-first:nth-child(odd)) {
    padding-top: 80px;
}

.window {
    position: relative;
    width: 160px;
    height: 187px;
    margin: 0 auto 20px;
    background-image: url("../../assets/images/window.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;
}

.windowPreviewVideo {
    position: absolute;
    top: 28px;
    left: 36px;
    width: 84px;
    height: 106px;
    overflow: hidden;
    opacity: 0;
    transition: opacity 1.5s ease-out;
    cursor: pointer;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 50% 50%;
    background-image: url("../../assets/images/preloader.gif");
}

.windowPreviewVideo:hover .playIcon {
    opacity: 1;
}

.windowPreviewVideo video {
    position: absolute;
    width: 84px;
    height: 106px;
    right: 0;
    bottom: 0;
    transform: translateX(calc((100% - 84px) / 2)) translateY(calc((100% - 106px) / 2));
}

.windowPreviewVideoShow{
    opacity: 1;
    transition: opacity 1.5s ease-in 0.2s;
}

.person {
    position: relative;
    top: 12px;
    opacity: 0.2;
}

.personShow {
    top: 0;
    opacity: 1;
    transition: opacity 1s ease-in 0.2s, top 0.4s ease-in 0.1s;
}

.personTitle {
    font-family: var(--font-lato);
    text-transform: uppercase;
    color: var(--c-white);
    margin-bottom: 9px;
    text-align: center;
}

.personName {
    font-size: 10px;
    font-weight: 500;
    line-height: 1;
    margin-bottom: 8px;
}

.personSurname {
    font-size: 22px;
    font-weight: 900;
    line-height: 1;
    color: var(--c-surname)
}

:root :global(.videoActive) .personSurname {
    color: var(--c-white)
}

.personSurnameMiddle {
    font-size: 18px;
}

.personPosition {
    width: 80%;
    max-width: 280px;
    margin: 0 auto 12px;
    text-align: center;
    font-family: var(--font-lato);
    font-size: 8px;
    font-weight: 900;
    line-height: 16px;
    text-transform: uppercase;
    color: var(--c-white);
}

.lineBox {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
}

.lineBox::before,
.lineBox::after {
    display: block;
    content: '';
    width: 74px;
    height: 1px;
    background-color: var(--c-white);
}

.lineBoxDot {
    position: relative;
    width: 6px;
    height: 6px;
    margin: 0 6px;
}

.lineBoxDot svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.articleTitle {
    color: var(--c-white);
    font-size: 16px;
    font-family: var(--font-lato);
    font-weight: 300;
}

.articleTitle::after {
    content:'';
    display: block;
    width: 166px;
    margin: 0 auto;
    height: 1px;
    background-color: var(--c-white);
}

.articleTitleText {
    padding: 10px 0;
    line-height: 1;
    position: relative;
    top: -2px;
    text-align: center;
}

.modal :global(.ant-modal-content) {
    border-radius: initial;
}

.modal :global(.ant-modal-close) {
    display: none;
}

body :global(.ant-modal) {
    margin: 0 auto;
    max-width: 100%;
}

body :global(.ant-modal-mask) {
    background: rgba(42, 12, 62, 0.8);
}

.closeModal {
    width: 40px;
    height: 40px;
    position: absolute;
    top: 20px;
    right: 20px;
    z-index: 10100;
    border: 0 none;
    background: none;
    cursor: pointer;
    outline: none;
}

.closeModalIcon {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.closeModalIcon svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.articleModal .closeModal {
    /*display: none;*/
    position: fixed;
}

.detailArticleWrapper {
    background: var(--c-white);
}

.modalInner {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    overflow: hidden;
}

.detailVideoWrapper {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
}

.detailVideoWrapper video {
    object-fit: cover;
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
}

.articleModal :global(.ant-modal-content),
.videoModal :global(.ant-modal-content) {
    background: none;
}

.mobileModal :global(.ant-modal-content) {
    display: flex;
    min-height: 100vh;
    background: none;
}

.mobileModal :global(.ant-modal-body) {
    flex-grow: 1;
}

.playIcon {
    display: none;
    position: absolute;
    width: 40px;
    height: 40px;
    z-index: 2;
    top: 50%;
    left: -20px;
    margin-left: 50%;
    margin-top: -20px;
    opacity: 0;
    transition: opacity 0.3s ease-in;
}

.playIcon svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.panelCenter {
    display: none;
    position: absolute;
    top: 0;
    width: 100%;
    height: 1px;
    background: red;
    z-index: 1;
}

.root :global(.panel-first) .panelCenter {
    top: 35%;
}

.menu {
    display: none;
    position: relative;
    top: 3.2vh;
    align-content: center;
    align-items: center;
}

.menuSection {
    display: flex;
    align-content: center;
    align-items: center;
    flex-grow: 1;
    min-width: 50%;
    padding-left: 6vw;
}

.menuSection:last-child {
    padding-left: 0;
    padding-right: 6vw;
    justify-content: flex-end;
}

.menuItemTitle {
    position: relative;
    pointer-events: auto;
    color: var(--c-white);
    text-transform: uppercase;
    line-height: 1;
    font-family: var(--font-lato);
    font-size: 13px;
    font-weight: 600;
    margin: 0 16px 0 14px;
    cursor: pointer;
}

.menuItemTitleActive {
    position: relative;
    z-index: 100000;
}

.menuItemRhombus {
    position: absolute;
    margin-left: 100%;
    top: 2px;
    left: 10px;
    display: inline-block;
    width: 10px;
    height: 10px;
}

.menuItemRhombus svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.carouselWrapper {
    display: flex;
    align-items: center;
    align-content: center;
    height: 100%;
}

.carouselWrapperInner {
    width: 100%;
}

.modalTitle {
    font-family: var(--font-lato);
    font-size: 24px;
    font-weight: 700;
    text-transform: uppercase;
    color: var(--c-white);
    text-align: center;
    margin-bottom: 20px;
}

.openMobileModal {
    position: absolute;
    top: 28px;
    left: 40px;
    width: 13px;
    height: 24px;
    background: none;
    margin: 0;
    padding: 0;
    border: 0 none;
    outline: none;
    cursor: pointer;
}

.openMobileModalIcon {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

.openMobileModalIcon svg {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

@media screen and (min-width: 768px) {

    .sectionParapet {
        height: 106px;
        bottom: -22px;
    }

    .root :global(.panel-first:nth-child(odd)) {
        padding-top: 98px;
    }

    .root :global(.panel-last),
    .root :global(.panel-last:nth-child(even)){
        padding-bottom: 98px;
    }

    .section :global(.panel:nth-child(even)) {
        padding-bottom: 98px;
    }

    .window {
        width: 384px;
        height: 448px;
        margin-bottom: 26px;
    }

    .windowPreviewVideo {
        width: 192px;
        height: 241px;
        top: 73px;
        left: 93px;
    }

    .windowPreviewVideo video {
        width: 192px;
        height: 241px;
        transform: translateX(calc((100% - 192px) / 2)) translateY(calc((100% - 241px) / 2));
    }

    .personTitle {
        margin-bottom: 18px;
    }

    .personName {
        font-size: 18px;
        margin-bottom: 18px;
    }

    .personSurname {
        font-size: 56px;
    }

    .personSurnameMiddle {
        font-size: 48px;
    }

    .personPosition {
        width: 46%;
        max-width: 462px;
        font-size: 14px;
        line-height: 24px;
        margin-bottom: 24px;
    }

    .lineBox::before,
    .lineBox::after {
        width: 176px;
        height: 2px;
    }

    .lineBoxDot {
        width: 12px;
        height: 12px;
    }

    .articleTitle {
        font-size: 28px;
        cursor: pointer;
    }

    .articleTitleText {
        top: -5px;
        padding: 15px 0;
    }

    .articleTitle::after {
        height: 2px;
        width: 378px;
    }

    .closeModal {
        top: 40px;
        right: 40px;
        width: 90px;
        height: 90px;
    }
}


@media screen and (min-width: 1024px) {

    :root :global(.overflowed) .header {
        width: calc(100% - 15px);
    }

    .corner,
    .burger {
        display: none;
    }

    .lineTop,
    .lineBottom {
        display: block;
    }

    .lineBottom {
        left: 16vw;
        right: 16vw;
        bottom: 4vh;
        display: none;
    }

    .lineLeft,
    .lineRight {
        display: none;
    }

    .sectionInner::after,
    .sectionInner::before {
        width: 70px;
    }

    .logo {
        display: block;
        width: 212px;
        height: 138px;
        top: 4vh;
        left: -106px;
    }

    .sectionParapet {
        bottom: -32px;
        height: 71px;
    }

    .sectionInner {
        padding-bottom: 62px;
    }

    .sectionInner::after,
    .sectionInner::before {
        width: 47px;
    }

    .root :global(.panel) {
        display: flex;
        justify-content: center;
        align-content: center;
        position: relative;
        width: 100%;
        padding-top: 70px;
        padding-bottom: 40px;
        text-align: center;
    }

    .section :global(.panel:nth-child(even)) {
        padding-bottom: 20px;
    }

    .root :global(.panel-first),
    .root :global(.panel-last) {
        align-items: center;
        height: 94vh;
        min-height: 300px;
    }

    .window,
    .person {
        margin: 0 128px;
    }

    .window {
        min-width: 256px;
        min-height: 300px;
        max-width: 256px;
        max-height: 300px;
        width: auto;
        height: auto;
    }

    .windowPreviewVideo {
        width: 130px;
        height: 162px;
        top: 49px;
        left: 61px;
    }

    .windowPreviewVideo video {
        width: 130px;
        height: 162px;
        transform: translateX(calc((100% - 130px) / 2)) translateY(calc((100% - 162px) / 2));
    }

    .section :global(.panel:nth-child(even)) .person {
        order: -1;
    }

    .person {
        max-width: 256px;
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        align-content: center;
    }

    .personTitle {
    }

    .personName {
        font-size: 14px;
    }

    .personSurname {
        font-size: 32px;
    }

    .personSurnameMiddle {
        font-size: 26px;
    }

    .personPosition {
        font-size: 10px;
        width: 100%;
    }

    .articleTitle {
        font-size: 20px;
        max-width: 256px;
    }

    .articleTitle::after {
        width: 100%;
    }

    .floor {
        position: relative;
        display: block;
        width: 106px;
        height: 208px;
        left: -53px;
        margin-top: -104px;
        top: 50%;
    }

    .floorRound svg :global(path.part) {
        visibility: visible;
    }

    .floorValue {
        display: block;
    }

    .floorCurrent {
        font-size: 40px;
        margin-top: -21px;
    }

    .floorCurrentMiddle {
        font-size: 36px;
        margin-top: -19px;
        left: -32px;
    }

    .floorPrev,
    .floorNext {
        left: -30px;
        font-size: 14px;
    }

    .floorPrev {
        top: 28px;
    }

    .floorNext {
        margin-top: -42px;
    }

    .floorPrevMiddle,
    .floorNextMiddle {
        font-size: 11px;
    }

    .floorPrev.floorPrevMiddle {
        top: 25px;
    }

    .floorNext.floorNextMiddle {
        margin-top: -36px;
    }

    .lineBox::before,
    .lineBox::after {
        width: 116px;
        height: 2px;
    }

    .lineBoxDot {
        width: 12px;
        height: 12px;
    }

    .modalArticleInner {
        width: 50%;
        transform: translateX(100%);
    }

    .modalArticleInnerOdd {
        transform: translateX(0);
    }

    .detailVideoWrapper {
        width: 50%;
        right: auto;
        background-position: 50% 50%;
        background-image: url("../../assets/images/preloader.gif");
        background-repeat: no-repeat;
        background-size: 100px;
    }

    .modalInnerOdd .detailVideoWrapper {
        left: 50%
    }

    .detailVideoWrapper video {
        transform: translateX(calc((50% - 100vw) / 2))
    }

    :root :global(.articleActive) .windowShow,
    :root :global(.videoActive) .personShow {
        z-index: 10000;
    }

    :root :global(.articleActive) .detailArticleWrapper,
    :root :global(.videoActive) .detailVideoWrapper {
        z-index: 10100;
    }

    .closeModal {
        width: 60px;
        height: 60px;
        left: -30px;
        right: auto;
        margin-left: 50%;
        top: 50%;
        margin-top: -30px;
    }

    .closeModalTopRight {
        left: auto;
        top: 40px;
        right: 40px;
        margin-left: 0;
        margin-top: 0;
    }

    .articleModal .closeModal {
        display: block;
    }

    .playIcon {
        display: block;
    }

    .lineTopLeft {
        left: 28.5vw;
        right: 52.5vw;
    }

    .lineTopRight {
        left: 52.5vw;
        right: 24.7vw;
    }

    .gotoRG {
        display: block;
    }

    .menu {
        display: flex;
    }

    .modalTitle {
        display: none;
    }

    .openMobileModal {
        display: none;
    }

    .borderLayerFooter {
        left: 4vw;
        right: 2vw;
