.root {
    display: none;
    align-items: center;
    align-content: center;
}

.share {
    position: relative;
    margin-left: 20px;
    margin-right: 20px;
    width: 156px;
    height: 32px;
    cursor: pointer;
    display: none;
}

.share svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

@media screen and (min-width: 1024px) {
    .root {
        display: flex;
        min-height: 32px;
    }

    .share {
        width: 123px;
        height: 22px;
    }
}

@media screen and (min-width: 1920px) {
    .share {
        width: 156px;
        height: 32px;
    }
}