.videoWrapper {
    position: relative;
    margin: 0 auto;
    width: 240px;
    height: 136px;
    background-color: var(--c-black);
}

.videoWrapperMiddle {
    width: 240px;
    height: 136px;
}

.video {
    border: 0 none;
    outline: none;
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.title {
    font-size: 30px;
    font-family: var(--font-lato);
    font-weight: 600;
    text-transform: uppercase;
    color: var(--c-white);
    text-align: center;
    margin-bottom: 16px;
}

.gotoLinkWrapper {
    text-align: center;
    padding-top: 70px;
}

.gotoLink {
    position: relative;
    width: 150px;
    height: 30px;
    display: inline-block;
}

.gotoLink svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

@media screen and (min-width: 768px) {
    .videoWrapper {
        width: 480px;
        height: 260px;
    }

    .videoWrapperMiddle {
        width: 480px;
        height: 260px;
    }
}

@media screen and (min-width: 1024px) {
    .videoWrapper {
        width: 860px;
        height: 480px;
    }

    .videoWrapperMiddle {
        width: 760px;
        height: 460px;
    }

    .gotoLinkWrapper {
        padding-top: 30px;
    }
}

@media screen and (min-width: 1280px) {
    .videoWrapper {
        width: 960px;
        height: 520px;
    }

    .videoWrapperMiddle {
        width: 860px;
        height: 500px;
    }
}


@media screen and (min-width: 1920px) {
    .videoWrapper {
        width: 1328px;
        height: 750px;
    }

    .videoWrapperMiddle {
        width: 1023px;
        height: 578px;
    }

}