.root :global(.react-multiple-carousel__arrow::before) {
    content: '';
}

.root :global(.react-multi-carousel-track) {
    align-items: center;
    align-content: center;
}

.item {
    user-select: none;
    padding: 40px 0;
    display: flex;
    align-items: center;
    align-content: center;
    min-height: 300px;
}

.itemCurrent {
    position: relative;
    z-index: 10;
}

.item .img {
    margin: 0 auto;
    display: block;
    min-width: 106px;
    min-height: 146px;
    max-width: 106px;
    max-height: 146px;
    transform: scale3d(1.132075471698113, 1.136986301369863, 1) rotate(0deg);
    opacity: 0.4;
    transition: transform 0.3s ease-out, opacity 0.3s ease-out;
}

.itemCurrent .img {
    transform: scale3d(1.415094339622642, 1.423904109589041, 1) rotate(0deg);
    transition: transform 0.3s ease-out, opacity 0.3s ease-out;
    opacity: 1;
    visibility: visible;
}

.itemCurrent .download {
    display: block;
    visibility: visible;
    opacity: 1;
    transition: visibility 0.3s ease-in, opacity 0.3s ease-in;
}

.itemCurrentPrev .img {
    transform: rotate(-7deg) translateX(-23px);
}

.itemCurrentNext .img {
    transform: rotate(7deg) translateX(23px);
}

.itemCurrentPrev .img,
.itemCurrentNext .img {
    transition: transform 0.3s ease-out;
}

.download {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    top: 100%;
    left: 50%;
    margin-top: -30px;
    margin-left: -75px;
    width: 150px;
    height: 30px;
    transition: visibility 0.3s ease-out, opacity 0.3s ease-out;
}

.downloadIcon,
.downloadIcon svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.link {
    background: var(--c-white);
}

.arrow {
    display: none;
    position: absolute;
    top: 50%;
    left: -23px;
    margin-left: 50%;
    margin-top: -45px;
    width: 46px;
    height: 90px;
    cursor: pointer;
}

.arrowPrev {
    left: -18vw;
}

.arrowPrev:hover svg {
    transform: translateX(-4px);
}

.arrowNext {
    left: calc(18vw - 46px);
}

.arrowNext:hover svg {
    transform: translateX(4px);
}

.arrow svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: transform 0.2s ease-in;
    transform: translateX(0);
}

@media screen and (min-width: 1024px) {
    .item {
        padding-bottom: 86px;
    }
    .item .img {
        min-width: 286px;
        max-width: 286px;
        min-height: 396px;
        max-height: 396px;
        transform: scale3d(0.8, 0.8, 1) rotate(0deg);
    }

    .itemCurrent .img {
        transform: scale3d(1.195804195804196, 1.194444444444444, 1) rotate(0deg);
    }

    .itemCurrentPrev .img {
        transform: rotate(-7deg);
    }

    .itemCurrentNext .img {
        transform: rotate(7deg);
    }
}

@media screen and (min-width: 1280px) {
    .arrow {
        display: block;
    }
}
