.root {
    padding: 130px 40px 0;
    font-family: var(--font-lato);
}

.menu {
    padding-top: 33px;
}

.menuItemTitle {
    font-size: 24px;
    font-weight: 900;
    line-height: 1;
    text-transform: uppercase;
    color: var(--c-white);
    margin-bottom: 40px;
    text-align: center;
    cursor: pointer;
}

.rhombusRibbon {
    width: 10px;
    height: 10px;
    margin: 0 auto;
    position: relative;
}

.rhombusRibbon::before,
.rhombusRibbon::after {
    content: '';
    position: absolute;
    top: 4px;
    left: 0;
    width: calc(50vw - 46px);
    height: 1px;
    background-color: var(--c-white);
}

.rhombusRibbon::before {
    left: calc(-50vw + 40px)
}

.rhombusRibbon::after {
    margin-left: 100%;
    left: 6px;
}

.rhombusRibbon svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.gotoRG {
    display: flex;
    align-content: center;
    align-items: center;
    justify-content: center;
    padding-top: 14px;
    padding-bottom: 18px;
    border-bottom: 1px solid var(--c-white);
}

.gotoRGLink {
    display: block;
    position: relative;
    min-width: 40px;
    min-height: 12px;
    max-width: 40px;
    max-height: 12px;
    margin-left: 10px;
}

.gotoRGLink svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.gotoRGtitle {
    font-size: 16px;
    font-weight: 600;
    color: var(--c-white);
    text-transform: uppercase;
    line-height: 1;
    min-height: 12px;
}

.mobileSocial {
    display: flex;
    justify-content: center;
    padding-top: 40px;
}