.root {
    padding: 135px 40px 40px;
    font-family: var(--font-lato);
    font-weight: 500;
    font-size: 12px;
    color: var(--c-white);
}

.rhombusRibbon {
    width: 10px;
    height: 10px;
    margin: 0 auto;
    position: relative;
}

.rhombusRibbon::before,
.rhombusRibbon::after {
    content: '';
    position: absolute;
    top: 4px;
    left: 0;
    width: calc(50vw - 46px);
    height: 1px;
    background-color: var(--c-white);
}

.rhombusRibbon::before {
    left: calc(-50vw + 40px)
}

.rhombusRibbon::after {
    margin-left: 100%;
    left: 6px;
}

.rhombusRibbon svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.item {
    text-align: center;
    padding: 23px 0 0;
    cursor: pointer;
}

.itemName {
    text-transform: uppercase;
}

.itemSurname {
    text-transform: uppercase;
    font-weight: 900;
    font-size: 24px;
    margin-bottom: 13px;
}

.value {
    position: relative;
    font-weight: 400;
    font-size: 10px;
    overflow: hidden;
}

.value::before,
.value::after {
    content: '';
    width: 50%;
    position: absolute;
    top: 10px;
    left: -10px;
    height: 1px;
    background-color: var(--c-white);
}

.value::after {
    right: -10px;
    left: auto;
}

.valueIndex {
    display: inline-block;
    width: 20px;
    height: 20px;
    line-height: 18px;
    border: 1px solid var(--c-white);
    border-radius: 10px;
}
