.root {
    font-family: var(--font-lato);
    font-size: 14px;
    font-weight: 300;
    padding: 0 32px;
}

.lineBox {
    margin: 0 auto;
    position: relative;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
}

.lineBox::before,
.lineBox::after {
    display: block;
    content: '';
    max-width: 430px;
    width: 100%;
    height: 1px;
    background-color: var(--c-black);
}

.lineBoxDot {
    position: relative;
    width: 20px;
    height: 21px;
    margin: 0 10px;
}

.lineBoxDot svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.lineBoxDot svg rect {
    fill: var(--c-black);
}

.personInfoHeader {
    padding: 30px 0 0;
}

.personInfoTitle {
    display: flex;
    align-items: baseline;
    justify-content: center;
    text-transform: uppercase;
    margin-bottom: 18px;
}

.personInfoName {
    line-height: 1;
}

.personInfoSurname {
    text-transform: uppercase;
    font-weight: 900;
    margin-left: 6px;
    line-height: 1;
}

.personInfoPosition {
    font-size: 8px;
    text-transform: uppercase;
    text-align: center;
    font-weight: 600;
    line-height: 14px;
    margin-bottom: 24px;
}

.personInfoDescription {
    position: relative;
    font-size: 18px;
    font-weight: 300;
    text-align: center;
    max-width: 900px;
    margin: 0 auto 24px;
    padding: 20px 0 10px;
    line-height: 24px;
}

.personInfoDescription::after {
    content: '';
    position: absolute;
    top: 100%;
    width: 100%;
    max-width: 500px;
    transform: translateX(-50%);
    height: 1px;
    background-color: var(--c-black);
}

.personInfoArticle {

}

.personInfoArticleTitle {
    font-size: 14px;
    font-weight: 900;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 20px;
}

.personInfoArticleContent {
    font-size: 12px;
    font-weight: 400;
    max-width: 900px;
    margin: 0 auto;
    padding-bottom: 40px;
}

.personInfoArticleContent p {
    margin: 0;
    margin-top: 20px;
}

.personInfoArticleContent p:first-child {
    margin-top: 0;
}

@media screen and (min-width: 768px) {
    .root {
        font-size: 20px;
        padding-left: 51px;
        padding-right: 51px;
    }

    .personInfoPosition {
        font-size: 10px;
        margin-bottom: 34px;
    }

    .lineBox::before,
    .lineBox::after {
        height: 2px;
    }

    .personInfoDescription {
        font-size: 30px;
        line-height: 34px;
        padding: 30px 0;
        margin-bottom: 50px;
    }

    .personInfoDescription::after {
        height: 2px;
        max-width: 800px;
    }

    .personInfoArticleTitle {
        font-size: 20px;
        margin-bottom: 40px;
    }

    .personInfoArticleContent {
        font-size: 18px;
    }
}

@media screen and (min-width: 1024px) {
    .root {
        font-size: 16px;
        padding-left: 24px;
        padding-right: 24px;
    }

    .personInfoPosition {
        font-size: 11px;
        line-height: 18px;
        margin-bottom: 34px;
    }

    .personInfoDescription {
        font-size: 22px;
        line-height: 28px;
        padding: 16px 0;
    }

    .lineBox::before,
    .lineBox::after {
        height: 1px;
    }

    .personInfoDescription::after {
        height: 1px;
    }

    .personInfoArticleTitle {
        font-size: 16px;
        margin-bottom: 30px;
    }

    .personInfoArticleContent {
        font-size: 14px;
    }

}

@media screen and (min-width: 1280px) {
    .root {
        font-size: 18px;
        padding-left: 60px;
        padding-right: 60px;
    }

    .personInfoPosition {
        font-size: 12px;
        line-height: 20px;
    }

    .personInfoDescription {
        font-size: 26px;
        line-height: 30px;
        padding: 16px 0;
    }

    .lineBox::before,
    .lineBox::after {
        height: 2px;
    }

    .personInfoDescription::after {
        height: 2px;
    }

    .personInfoArticleTitle {
        font-size: 18px;
        margin-bottom: 32px;
    }

    .personInfoArticleContent {
        font-size: 16px;
    }

}

@media screen and (min-width: 1280px) {
    .root {
        font-size: 24px;
        padding-left: 80px;
        padding-right: 80px;
    }

    .personInfoSurnameMiddle {
        font-size: 18px;
    }

    .personInfoPosition {
        font-size: 14px;
        line-height: 22px;
    }

    .personInfoDescription {
        font-size: 38px;
        line-height: 44px;
        padding: 22px 0;
    }

    .lineBox::before,
    .lineBox::after {
        height: 2px;
    }

    .personInfoDescription::after {
        height: 2px;
    }

    .personInfoArticleTitle {
        font-size: 24px;
        margin-bottom: 36px;
    }

    .personInfoArticleContent {
        font-size: 21px;
    }

}